<template>
  <div class="home" style="max-width:750px;margin:0 auto;">
    <!--  -->
    <navBar class="nav" :arrow="3">
      <p class="navTitle" slot="tabContent">{{navTitle}}</p>
    </navBar>
    <van-count-down
      v-if="time"
      :auto-start="false"
      class="CountDown"
      @finish="timeOut"
      :time="time"
      ref="countDown"
    />
    <!--  -->
    <div class="bg_img"></div>

    <div
      class="test"
      @click.once="startTime"
      v-if="questionList.length&&!Object.keys(result).length"
    >
      <div class="title ali-c">
        <div class="leftTitle ali-c">
          <img src="@/assets/b_label@2x.png" alt />
          <p v-if="indexQuestion.typedata == 1"></p>
          <p v-if="indexQuestion.typedata == 2"></p>
          <p v-if="indexQuestion.typedata == 4"></p>
        </div>
        <div class="count ali-c">
          <span class="n">{{index + 1}}</span>
          <span class="c">/{{questionList.length}}</span>
        </div>
      </div>

      <!--    ()    key(kye) -->
      <div v-if="indexQuestion.typedata==1||indexQuestion.typedata==2">
        <choice
          :testData="indexQuestion"
          :answerInfo="answerInfo"
          :key="indexQuestion.id"
          :testIndex="index"
          @selectEvent="selectEvent"
        ></choice>
      </div>

      <div v-if="indexQuestion.typedata==4">
        <analysisChoice
          :testData="indexQuestion"
          :answerInfo="answerInfo"
          :key="indexQuestion.id"
          :testIndex="index"
          @selectEvent="selectEvent"
        ></analysisChoice>
      </div>
      <div class="btn ali-c">
        <div class="a"></div>
        <div class="confirm" @click="confirm" v-if="btnShow&&!nextShow"></div>
        <div
          class="confirm"
          @click="nextQues"
          v-else-if="nextShow&&index+1!==questionList.length"
        ></div>
        <div class="confirm" @click="submitTest" v-if="submitShow"></div>
      </div>
    </div>

    <testResult v-if="Object.keys(result).length" @getStatus="getData" :result="result"></testResult>

    <van-popup :close-on-click-overlay="false" v-model="applyShow">
      <div class="applyTest">
        <p></p>
        <div class="applyInput ali-c">
          <input type="text" v-model="applyName" placeholder="" />
          <input type="tel" v-model="applyPhone" maxlength="11" placeholder="" />
        </div>
        <div class="applyBtn" @click="applyTest"></div>
      </div>
    </van-popup>

    <tabBar class="van-hairline--top" :status="1"></tabBar>
  </div>
</template>

<script>
import Vue from "vue";
import { Toast, Dialog, CountDown } from "vant";
import {
  getstate,
  getQues,
  upAnsw,
  submit,
  answerApply,
} from "@/request/api.js";
import navBar from "@/components/navBar/navBar"; //
import tabBar from "@/components/tabBar/tabBar"; //

import choice from "@/components/choice/choice"; //
import analysisChoice from "@/components/analysisChoice/analysisChoice"; //
import testResult from "@/components/testResult/testResult"; //

Vue.use(Toast, Dialog, CountDown);
export default {
  name: "home",
  components: {
    navBar,
    tabBar,
    choice,
    analysisChoice,
    testResult,
    "van-count-down": CountDown,
  },
  data() {
    return {
      //------------------------------------------
      questionList: [], // 
      index: 0, //
      selectAnswer: {}, //<<<<<<<<<<<<ID   >>>>>>>>>>>>>
      //------------------------------------------
      answerInfo: {}, //
      submitShow: false, //
      //------------------------------------------
      correct_number: 0, // 
      allScore: 0, //
      //-----------------------------------------
      navTitle: "",
      //-----------------------------------------
      testStatus: "", //padding need_apply success fali
      applyShow: false, //
      applyName: "", //
      applyPhone: "", //
      //------------------------------------------
      Timing: "", //,,
      isTest: false, //true      true
      testStartTime: 0, //
      testEndTime: 0, //
      //------------------------------------------
      result: {}, //
    };
  },
  watch: {},
  computed: {
    //
    time() {
      if (this.Timing) {
        return Number(this.Timing) * 60 * 1000;
      } else {
        return 0;
      }
    },
    //btn
    btnShow() {
      if (this.selectAnswer.testType == 1) {
        //
        return this.selectAnswer.select.length == 1 ? true : false;
      } else if (this.selectAnswer.testType == 2) {
        //
        return this.selectAnswer.select.length >= 1 ? true : false;
      } else if (Array.isArray(this.selectAnswer)) {
        return this.selectAnswer.every((v) => v.select.length >= 1);
      }
    },
    //
    nextShow() {
      return Object.keys(this.answerInfo).length > 1 ? true : false;
    },
    //
    indexQuestion() {
      return this.questionList[this.index];
    },
  },
  methods: {
    // 
    getData() {
      this.$loading();
      return getstate().then((res) => {
        if (res.code == 1) {
          console.log(res.data);
          Toast.clear();
          if (res.data.status == "pending") {
            //
            this.$router.replace({
              path: "/applying",
              query: {
                msg: res.data.msg,
              },
            });
            localStorage.clear();
            return "pending";
          }
          if (res.data.status == "fail") {
            //
            this.$router.replace({
              path: "/reviewFail",
              query: {
                msg: res.data.msg,
              },
            });
            localStorage.clear();
            return "fail";
          }
          if (res.data.status == "need_apply") {
            //
            this.applyShow = true;
            return "need_apply";
          }
          if (res.data.status == "success") {
            //
            Dialog.alert({
              message: res.data.msg,
            }).then(() => {
              this.$router.replace("/profile");
            });
            return "success";
          }
          if (res.data.status == "need_wait") {
            //
            Dialog.alert({
              message: res.data.msg,
            }).then(() => {
              this.$router.replace("/profile");
            });
            return "need_wait";
          }
          if (res.data.status == "can") {
            return "can";
          }
        }
      });
    },
    // 
    getQuestion() {
      this.$loading();
      getQues()
        .then((res) => {
          Toast.clear();
          if (res.code == 1) {
            this.Timing = res.data.anwser_times;
            this.questionList = res.data.questionList;
          } else {
            //
            Toast.fail(res.msg);
            setTimeout(() => {
              localStorage.clear();
              this.$router.replace("/index/login");
            }, 1500);
          }
        })
        .catch((err) => {
          Toast.clear();
          Toast.fail(",");
        });
    },
    //
    confirm() {
      let data = this.selectAnswer;
      if (Array.isArray(data)) {
        //
        if (data.length < this.indexQuestion.option.length) {
          Toast.fail("!");
          return;
        }
        let v = data;
        for (let i = 0; i < v.length; i++) {
          if (!v[i]) {
            Toast.fail("!");
            return;
          }
          if (v[i].testType == 1 && v[i].select.length == 0) {
            Toast.fail("!");
            return;
          }
          if (v[i].testType == 2 && v[i].select.length <= 1) {
            Toast.fail("");
            return;
          }
        }
        this.analysisConfirm(data);
      } else if (typeof data == "object" && !Array.isArray(data)) {
        if (data.testType == 1) {
          if (data.select.length >= 1) {
            this.choiceConfirm(data);
          } else {
            Toast.fail("");
          }
        } else if (data.testType == 2) {
          if (data.select.length >= 2) {
            this.choiceConfirm(data);
          } else {
            Toast.fail("");
          }
        }
      }
    },
    //!,
    choiceConfirm(answers) {
      //
      this.$loading();
      upAnsw({
        question_id: answers.id,
        user_answ: answers.select.join("|"),
      })
        .then((res) => {
          Toast.clear();
          if (res.code == 1) {
            //
            this.answerInfo = res.data.questionSingle;
            this.isFinally();
            if (res.data.questionSingle.is_correct == 1) {
              //
              this.correct_number += 1;
              //1
              this.allScore += Number(res.data.questionSingle.score);
            }
          }
        })
        .catch((err) => {
          Toast.clear();
          Toast.fail(",");
        });
    },
    //!
    async analysisConfirm(v) {
      let results = [];
      this.$loading();
      for (let i = 0; i < v.length; i++) {
        await upAnsw({
          question_id: v[i].id,
          user_answ: v[i].select.join("|"),
        }).then((res) => {
          if (res.code == 1) {
            results.push(res.data.questionSingle);
            if (res.data.questionSingle.is_correct == 1) {
              //
              this.correct_number += 1;
              //1
              this.allScore += Number(res.data.questionSingle.score);
            }
          }
        });
      }
      Toast.clear();
      this.answerInfo = results;
      this.isFinally();
    },
    // 
    nextQues() {
      this.answerInfo = {};
      this.selectAnswer = {};
      this.index += 1;
    },
    //
    isFinally() {
      //
      if (this.index + 1 == this.questionList.length) {
        this.submitShow = true;
      }
    },
    //
    submitTest() {
      this.testEndTime = parseInt(new Date().getTime() / 1000);
      //
      let allTestNum = 0;
      this.questionList.forEach((v) => {
        if (v.typedata == 1 || v.typedata == 2) {
          allTestNum += 1;
        } else if (v.typedata == 4) {
          allTestNum += v.option.length;
        }
      });
      submit({
        correct_number: this.correct_number, //
        score: this.allScore, //
        start_time: this.testStartTime, //
        end_time: this.testEndTime, //
        all_number: allTestNum, //
      })
        .then((res) => {
          if (res.code == 1) {
            console.log(res);
            this.result = res.data.complete; //
            this.$refs.countDown.pause(); //
            this.isTest = false; //!
            this.navTitle = "";
          }
        })
        .catch((err) => {
          Toast.clear();
          Toast.fail(",");
        });
    },
    //
    selectEvent(data) {
      //
      this.selectAnswer = data;
    },
    //
    timeOut() {
      Dialog.alert({
        message:
          "",
      }).then(() => {
        //
        this.$router.go(0);
      });
    },
    //
    startTime() {
      this.testStartTime = parseInt(new Date().getTime() / 1000); //
      this.$refs.countDown.start(); //
      this.isTest = true; //
    },
    //
    applyTest() {
      if (!this.applyName) {
        Toast.fail("");
        return;
      }
      if (!this.applyPhone || this.applyPhone.length !== 11) {
        console.log(this.applyPhone);
        Toast.fail("");
        return;
      }
      answerApply({
        name: this.applyName,
        mobile: this.applyPhone,
      }).then((res) => {
        if (res.code == 1) {
          Toast.success(res.msg);
          setTimeout(() => {
            this.$router.replace("/profile");
          }, 1500);
        }
      });
    },
  },
  created() {
    this.getData()
      .then((status) => {
        if (status !== "pending" && status !== "fail") {
          this.getQuestion();
        }
      })
      .catch((err) => {
        Toast.clear();
        Toast.fail(",");
      });
  },
  mounted() {},
  beforeRouteLeave(to, from, next) {
    //
    if (to.name == "profile" && from.name == "home" && this.isTest) {
      Dialog.confirm({
        title: "",
        message: "",
      })
        .then(() => {
          next();
        })
        .catch(() => {});
    } else {
      next();
    }
  },
};
</script>

<style scoped lang="less">
.home {
  background-color: #f5f5f5;
  position: relative;
  padding-bottom: 1.5rem;
  min-height: 100vh;
  .nav {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 10;
    .navTitle {
      font-size: 0.48rem;
      font-family: PingFang SC;
      font-weight: bold;
      color: white;
    }
  }
  .bg_img {
    height: 5rem;
    background: url("~@/assets/b_bg@2x.png");
    background-size: 100%;
    background-repeat: no-repeat;
  }
  .test {
    width: 9rem;
    margin: 0 auto;
    margin-top: -1.75rem;
    border-radius: 0.3rem;
    overflow: hidden;
    box-shadow: 0rem 0.1rem 0.1rem 0rem rgb(200, 200, 200);
    margin-bottom: 1rem;
    .title {
      background: #ffffff;
      height: 1.3rem;
      padding: 0 0.3rem;
      border-bottom: 0.02rem solid #eeeeee;
      .leftTitle {
        flex: 1;
        img {
          width: 0.7rem;
        }
        p {
          padding-left: 0.1rem;
          font-size: 0.43rem;
          font-family: PingFang SC;
          font-weight: bold;
          color: #343434;
        }
      }
      .count {
        height: 1.3rem;
        .n {
          font-size: 0.67rem;
          font-family: DINCond-Bold;
          font-weight: 400;
          color: #343434;
        }
        .c {
          font-size: 0.48rem;
          font-family: DINCond-Bold;
          font-weight: 400;
          color: #9a9a9a;
        }
      }
    }
  }

  .CountDown {
    position: absolute;
    display: block;
    width: 100%;
    top: 1.5rem;
    z-index: 1;
    text-align: center;
    color: white;
  }

  .btn {
    height: 1.5rem;
    background-color: white;
    padding: 0 0.7rem;
    .a {
      flex: 1;
    }
    div:active {
      opacity: 0.7;
    }
    .confirm {
      width: 1.6rem;
      height: 0.8rem;
      background: #3d80fc;
      border-radius: 0.5rem;
      color: white;
      text-align: center;
      line-height: 0.8rem;
    }
  }
  //
  .active {
    background: #d8ecff !important;
    border: 0.05rem solid #3e80fc !important;
    p {
      color: #0250a0 !important;
    }
  }

  //
  .fail {
    background: #fddadb;
    border: 0.02rem solid #ff5258;
    p {
      color: #df0202 !important;
    }
  }
  //
  .success {
    background: #dafdf0 !important;
    border: 0.02rem solid #4ee6af !important;
    p {
      color: #02ae4f !important;
    }
  }
  // 
  .tabBar_space {
    height: 1.3rem;
  }
  // 
  .answer_info {
    width: 9rem;
    margin-top: 0.3rem;
    padding: 0 0 0.5rem 0;
    background: #ffffff;
    border-radius: 0.3rem;
    box-shadow: 0rem 0.1rem 0.1rem 0rem rgb(200, 200, 200);
    .title {
      height: 1.3rem;
      padding: 0 0.3rem;
      border-bottom: 0.02rem solid #eee;
      img {
        width: 0.7rem;
      }
      p {
        padding-left: 0.1rem;
        font-size: 0.43rem;
        font-family: PingFang SC;
        font-weight: bold;
        color: #343434;
      }
    }
    .right_box {
      padding: 0.3rem;
      .right_answer {
        font-size: 0.4rem;
        font-family: PingFang SC;
        font-weight: bold;
        color: #343434;
      }
      .right_reason {
        margin-top: 0.2rem;
      }
    }
  }
  //
  .applyTest {
    width: 8rem;
    height: 7.2rem;
    background: #ffffff;
    border-radius: 1rem;
    padding: 0.9rem;
    text-align: center;
    p {
      font-size: 0.57rem;
      font-family: PingFang SC;
      font-weight: 500;
      color: #3e80fc;
    }
    .applyInput {
      margin-top: 0.8rem;
      height: 2.5rem;
      flex-wrap: wrap;
      input {
        width: 6.27rem;
        height: 0.94rem;
        background: #ffffff;
        border: 0.03rem solid #dedede;
        border-radius: 0.05rem;
        padding: 0 0.2rem;
      }
    }
    .applyBtn {
      margin-top: 0.8rem;
      width: 6.27rem;
      height: 1.07rem;
      background: #3e80fc;
      border-radius: 0.3rem;
      font-size: 0.4rem;
      font-family: PingFang SC;
      font-weight: 500;
      color: #ffffff;
      line-height: 1.07rem;
      &:active {
        opacity: 0.7;
      }
    }
  }
}
</style>
